.font-family-monospace {
  font-family: monospace;
}

.input-range {
  width: 85% !important;
  margin: 0 auto !important;
}

.input-range__slider-container {
  transition: left 0.1s ease-out !important;
}

.input-range__slider {
  transition: transform 0.1s ease-out, box-shadow 0.1s ease-out !important;
  z-index: 10;
  height: 3rem !important;
  width: 3rem !important;
}

.buffer-indicator-track .input-range__track--background {
  color: grey !important;
  background-color: grey !important;
}
/* .buffer-indicator-track .input-range__track {
  color: grey !important;
  background-color: grey !important;
} */

.buffer-indicator-track .input-range__slider-container {
  display: none !important;
  visibility: hidden !important;
}

.main-scroller .input-range__track {
  z-index: 1;
}

.buffer-indicator-track .input-range__track {
height: 10px !important;
}

.buffer-indicator-track .input-range__slider {
  height: 2rem !important;
  width: 2rem !important;
  margin-top: -1.6rem !important;
}

.input-range__slider:hover {
  background-color: #455acc;
}

.input-range__track {
  transition: left 0.1s ease-out, width 0.1s ease-out !important;
}

.input-range__slider {
  touch-action: pan-x;
}

.input-range__track {
  height: 1.2rem !important;
}

.input-range__slider {
  height: 2rem !important;
  width: 2rem !important;
  margin-top: -1.6rem !important;
}

.input-range__label-container {
  left: 0 !important;
  top: 35px !important;
}

@media screen and (max-width: 600px) {
  .input-range__slider {
    height: 2rem !important;
    width: 2rem !important;
    margin-top: -1.6rem !important;
  }

  .input-range__track {
    height: 1.3rem !important;
  }
}

.center {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
